.tour {
  position: relative;
  min-height: 412px;
  display: flex;
  align-items: end;
}

.tour--nojs {
  width: 31.66%;
  margin-right: 30px;

  @include vp-1199 {
    margin-right: 19px;
    width: 50%;
  }

  @include vp-767 {
    margin-right: 0;
    width: 100%;
  }
}

