.tour__text-wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  padding: 203px 20px 28px;
  top: 0;
  color: $color-default-white;
  overflow: auto;

  @include vp-1199 {
    padding-top: 206px;
  }

  @include vp-767 {
    padding-top: 197px;
  }
}

.tour__image-wrapper {
  width: 100%;
  height: auto;

  img {
    width: 100%;
  }
}

.tour__image-mask {
  position: absolute;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  background-image: $gradient-two;
}
